import React, { useContext, useEffect, useState } from 'react';
import { Context } from './context/context';
import Layout from './layout';
import { useNavigate } from 'react-router-dom';
import http from './config';


export default function Dashboard() {
  const navigate = useNavigate();
  // document.title = 'Dashboard';
  const user = useContext(Context);
  const [getdata, setgetdata] = useState([]);

  const openChildWindow = (url) => {
    const { ipcRenderer } = window.require('electron');
    ipcRenderer.send('openChildWindow', url);
  };

  useEffect(() => {
    async function fetchData() {
      try {
        const response = await http.get('/tools');

        if (response.status === 200) {
          const data = response.data;
          setgetdata(data);
        }
      } catch (error) {
        console.error('Error:', error);
        // You can handle the error here (e.g., show an error message)
      }
    }

    fetchData();
  }, []); // The empty dependency array ensures this effect runs only once on component mount

  console.log(user.user);

  if (!user.user) {
    navigate('/login')
  }

  return (
    <Layout>
      <div className="container-fluid">
        <div className="row" style={{margin: '0 10px'}}>
          {getdata.map((getdatas) => (

            <div class="col-xl-3 col-lg-4 col-sm-6">
              <div class="card">
                <div class="product-box"><a style={{cursor: "pointer"}} onClick={() => openChildWindow(getdatas.url)}>
                  <div class="product-img">
                    <img class="item img-fluid" src={getdatas.img_url} alt="semrush" title="semrush" height="auto" width="150px" />
                  </div>
                  <div class="product-details">
                    <h4 >{getdatas.title}</h4>
                  </div></a>
                </div>
              </div>
            </div>
          ))}

        </div>
      </div>
    </Layout>
  );
}
