import React from 'react';
import { Link} from 'react-router-dom'; // Import useHistory
import {useNavigate} from 'react-router-dom';
import {  useState as state} from 'react';
import { Context, useEffect } from './context/context';
import { useContext } from 'react';

export default function login() {
  
  const navigate = useNavigate();
  const {user, setUser} = useContext(Context);
  
  const [error, setError] = state(null);

  const [showPassword, setShowPassword] = state(false);
  
  const togglePasswordVisibility = () => {
  setShowPassword(!showPassword);
};
  
if (user && window.location.pathname === '/login') {
  // Redirect to the dashboard
  navigate('/dashboard');
}

const [email, setemail] = state();
  const [password, setPassword] = state();

  const handleSubmit = async (e) => {
    e.preventDefault();

    const credential = {
      email: email,
      password: password,
    };

    try {
      const response = await fetch('//app.toolszm.com/api/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(credential),
        credentials: 'include', // Include cookies with the request

      });

      if (response.ok) {
        const data = await response.json();
        // Handle successful login response here
        if (data.username) {
          setUser(data);  
          // Redirect to the dashboard
          navigate('/dashboard');
          useEffect()
        }

      } else {
        // Handle login error (e.g., incorrect credentials)
        const errorData = await response.json(); // Parse the error response
        
        if(errorData){
          setError(errorData.message)
        }
        console.log(errorData);
      }
    } catch (error) {
      // Handle network errors or other issues
      console.log('Error:', error);
    }
  };



  return (
    <>
    <div className="container-fluid p-0">
      <div className="row m-0">
        <div className="col-12 p-0">    
          <div className="login-card">
            <div>
              <div className='text-center'><img class="img-fluid for-light" style={{"height": "120px"}} src="logo.png" alt="Toolszm" /></div>
              <div className="login-main"> 
                <form className="theme-form" onSubmit={handleSubmit}>
                  <h4 className="text-center">Sign in to account</h4>
                  <p className="text-center">Enter your email &amp; password to login</p>
                  {error && <div className="alert alert-danger dark" role="alert"><p className=' m-0' style={{"color": "white"}}>{error}</p></div>}
                  <div className="form-group">
                    <label className="col-form-label">Email Address</label>
                    <input className="form-control" type="email" required="required"  placeholder="Test@gmail.com" onChange={(e) => setemail(e.target.value)}  />
                  </div>
                  <div className="form-group">
                    <label className="col-form-label">Password</label>
                    <div className="form-input position-relative">
                      <input className="form-control" type={showPassword ? 'text' : 'password'} name="login[password]" required="required" placeholder="*********" onChange={(e) => setPassword(e.target.value)} />
                      <div className="showPassword"><span className='show-hide' onClick={togglePasswordVisibility}> {showPassword ? 'Hide' : 'Show'}</span></div>
                    </div>
                  </div>
                  <div className="form-group mb-0">
                    <div className="text-end mt-3">
                      <button className="btn btn-primary btn-block w-100"  type="submit">Sign in                 </button>
                    </div>
                  </div>
                  
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
      </div>
    </>
  )
}
